.testimonial{
    display: flex;
    justify-content: center;
    margin:7em 0em 0em 0em;
    .t{
        color:aliceblue;
        font-size: 4em;
        font-weight: 500;
    }
}
.testimonial-c{
    display:flex;
    flex-wrap: wrap;
    justify-content:center;
    gap:5em;
    margin:3em 0em;
}
.testimage img{
    width: 100px;
    height:100px;
    border-radius: 50%;
    border: 10px solid orangered;
}
.test-content{
    display:flex;
    justify-content:center;
    gap:1em;
    align-items: center;
}
.test-title{
    font-size: 1.2em;
    color:white;
    .p-title{
        color:orangered;
        font-weight: 500;
    }
}