.max-width{
max-width:1100px;
margin:auto;
}
.div-c{
    display:flex;
    justify-content: space-between;
    gap:1em;
}
body{
    background-color: #292d33;;
}
.project-details{
    background:url('../background.jpg');
    background-position:50% 50% ;
    background-repeat: no-repeat;
    height:17em;
    opacity:.6;
}
.details{
    position: relative;
    top:5em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:2em;
    color:rgb(20, 18, 18);
    opacity: 2;
}
.details-content{
    display:flex;
    justify-content: space-between;
    align-items: center;
    font-size: 2em;
    font-weight: 300;
    gap:1.6em
}
.no{
    font-size: 1.6em;
    font-weight: 500;
}
.contact{
    display: flex;
    justify-content: center;
    gap:5em;
    max-width: 1200px;
    margin: auto;
}
.contact-form{
    margin:7em 5em;
}
.Social-icon{
    display: flex;
    align-items: center;
    gap:1em;
    font-size: 1.5em;
}
.newsletter{
    background-color: #f06e49;
    padding:2em 2em;
    border-radius: 30px;
    font-size: 1.5em;
    color:white;
    margin:7em 0em;
}
.form-details h2{
    text-align: center;
    color:white;
    font-size: 3em;
    font-weight: 500;
}
.f-email,.f-name{
    padding:.7em .5em;
    outline:none;
    border-radius: 20px;
    width:25em;
    font-size: 1.2em;
    margin-bottom: 1.5em;
}
.f-message{
    padding:.7em .5em;
    outline:none;
    border-radius: 20px;
    width:13em;
    font-size: 1.2em;
    margin-bottom: 1.5em;
}
.f-submit{
    padding:.7em .5em;
    border-radius: 20px;
    width:25em;
    font-size: 1.2em;
    margin-bottom: 1.5em;
    background-color: #f06e49;
    color: white;
}
@media only screen and (max-width: 600px) {
    .contact{
        flex-wrap: wrap;
    }
    .f-email,.f-name,.f-submit{
        width:15em;
    }
    .contact-form{
        margin:0em 0em;
    }
  }