.portfolio-c{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-size:5em;
  color:white;
}
.p-c{
    width: 50%;
    text-align: center;
    font-size: 30px;
}
.portfolio-d{
    margin-top: 12em;
    width: 100%;
}
.card-c{
display:flex;
justify-content: center;
gap:1.5em;
flex-wrap: wrap;
}
.card{
  background-color: #fff;
  border-radius: 18px;
padding-bottom: 1em;
}
.card img{
  height: 240px;
  width: 280px;
  border-radius: 18px 18px 0px 0px;
}
.card p{
  text-align: center;
  font-size: 1.5em;
  font-weight: 500;
}
.card div{
text-align: center;
font-size: 1em;
font-weight: 100;
}
.card:hover .card-w{
  position: relative;
  top:-.5em;
}
.content{
margin-top: 3em;
}
@media only screen and (max-width: 600px) {
 .portfolio-m{
  text-align: center;
 }  
}
