.bio{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:white;
    font-size:2em;
    }
    .bio p{
        width: 70%;
        text-align: center;
    }
    .bio .name{
        color:#f65921;
        font-size: 2em;
        font-weight: 600;
        margin-top: 3em;
        text-align: center;
    }
    .skills{
        display:flex;
        justify-content: center;
        gap:2em;
        margin:2em 0em;
        flex-wrap: wrap;
    }
    .circle{
        height: 10em;
        width: 10em;
        border-radius: 50%;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border:5px solid white;
    }
    .circle p{
        color:#f65921;
        font-size: 1.5em;
        font-weight:500;
    }
    .circle:hover{
       border-color: #f65921;
        position: relative;
        top:-.5em;
        transition-duration: .7s;
    }