.home{
    background:url('../front-i-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height:543px;
}
.logo-i{
    height:2em
}
.contact-d{
    font-size:1.2em;
    background-color: #f65921;
    padding:3em 2em;
    border-radius: 25px;
    margin:4em 0em 0em 0em;
}
.about{
display: flex;
justify-content: center;
}
.man{
    height:13em;
    border-radius: 50%;
}
.data-d{
    text-align: center;
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border:1px solid white;
    background-color: #fff;
    padding:7em 0em 2em 0em ;
    width:50%;
    border-radius: 22px;
}
.image{
position: relative;
top:6em
}
.contact-h{
    display:flex;
    justify-content: center;
    gap:1em;
    flex-wrap: wrap;
}
@media only screen and (max-width: 600px) {
   .data-d{
    width: 78%;
   }
   .home{
    height: 770px;
    max-width: 100%;
   }
  }